import React from "react";
import { Col, Container, Row, Form, Button, Table } from "react-bootstrap";

import Title from "../jsx/Title";

export default function Contact(props) {
  const { dataContact } = props;

  return (
    <>
      <Container>
        <Title title="Contact" />
        <div className="p-3">
          <Row>
            <Col md="4">
              <Form>
                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Email address</Form.Label>
                  <Form.Control type="email" placeholder="Enter email" />
                  <Form.Text className="text-muted">
                    We'll never share your email with anyone else.
                  </Form.Text>
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                  <Form.Label>Example textarea</Form.Label>
                  <Form.Control as="textarea" rows={3} />
                </Form.Group>
                <Button variant="primary" type="submit">
                  Submit
                </Button>
              </Form>
            </Col>
            <Col md="2"></Col>
            <br />
            <Col md="6">
              <h5 className="font-weight-bold">Contact me at</h5>
              <Table responsive="sm" borderless>
                <tbody>
                  {dataContact &&
                    dataContact.map((item) => {
                      return (
                        <tr>
                          <td>{item.title}</td>
                          <td>{item.a}</td>
                          <td>
                            <a
                              href={
                                item.title === "Hp/Wa/Tele"
                                  ? "https://wa.me/081224204657"
                                  : item.title === "Email"
                                  ? " mailto:aandi30082001@gmail.com"
                                  : item.link
                              }
                              target="_BLANK"
                            >
                              {item.link}
                            </a>
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </Col>
          </Row>
        </div>
      </Container>
    </>
  );
}
