import { Navbar as NavbarB4, Nav, Container, Image } from "react-bootstrap";
import imageAndi from "../../img/iconAndi.svg";

export default function Navbar(props) {
  const { menu } = props;
  return (
    <>
      <NavbarB4 bg="white" expand="lg" fixed="top">
        <Container>
          <NavbarB4.Brand href="#home">
            <Image src={imageAndi} height={15} rounded />
          </NavbarB4.Brand>
          <NavbarB4.Toggle aria-controls="basic-navbar-nav" />
          <NavbarB4.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              {menu &&
                menu.map((item) => {
                  return (
                    <Nav.Link
                      href={`#${item.key}`}
                      key={item.key}
                      className="mr-4 font-weight-bold"
                    >
                      {item.menu}
                    </Nav.Link>
                  );
                })}
            </Nav>
          </NavbarB4.Collapse>
        </Container>
      </NavbarB4>
    </>
  );
}
